.filterBarByPriceContainer{
    margin: 5px ;
    border: black solid 2px;
    background: brown;
}

.filterBarByPriceVisibleFalse{
    width: 300px;
    height: 40px;
    background: brown;
    transition: 0.5s;
}

.filterBarByPriceVisibleTrue{
    width: 300px;
    height: 200px;
    background: blueviolet;
    transition: 0.5s;
}

.openCloseFilterBarByPrice{
    border-top: solid black 2px;
}


